import React from "react"
import { useSelector } from "react-redux"
import { useMediaQuery } from "react-responsive"
import { useHistory } from "react-router"
import { AcceptIcon, CancelIcon, PopupBlockWrapper } from "../../../assets/svg"
import SlideInModal from "../../../components/SlideInModal"
import { getUserData } from "../../../store/user/selector"
import "./styles.scss"
const { innerWidth: width, innerHeight: height } = window
const NewbiePopup = () => {
  const userData = useSelector(getUserData)
  const history = useHistory()
  const isMobile = useMediaQuery({ maxWidth: 767 })
  const isTablet = useMediaQuery({ minWidth: 768, maxWidth: 1199 })
  const getModalWidth = () => {
    return !isMobile && !isTablet
      ? width * 0.3
      : isMobile
      ? width * 0.9
      : width * 0.6
  }
  const getModalHeight = () => {
    return !isMobile && !isTablet
      ? height * 0.5
      : isMobile
      ? height * 0.4
      : height * 0.4
  }
  return (
    <div
      className="popup-newbie-container"
      style={{ width: getModalWidth(), height: getModalHeight() }}
    >
      <PopupBlockWrapper width={getModalWidth()} height={getModalHeight()} />
      <div className="popup-newbie-content-wrapper">
        <p className="popup-newbie-title">Chc mng bn mi</p>
        <p className="popup-newbie-content">
          Cho mng bn  tham gia sn chi i S Siu Tr Nh Hc ng. BTC
           chun b v dnh tng cho bn nhng mn qu bt ng.  nhn qu,
          bn hy thc hin ngay nhim v c giao theo hng dn di y
          nh!!!
        </p>
        <p className="popup-newbie-subtitle">
          TI APP TM TR LC V TRI NGHIM SIU TR NH HC NG
        </p>
      </div>
      <div className="newbie-icon-wrapper">
        <div
          className="popup-newbie-button-wrapper margin-right"
          onClick={() => {
            SlideInModal.hide()
            setTimeout(() => {
              history.push("/inviter")
            }, 200)
          }}
        >
          <AcceptIcon />
        </div>
        <div
          className="popup-newbie-button-wrapper"
          onClick={() => {
            SlideInModal.hide()
          }}
        >
          <CancelIcon />
        </div>
      </div>
    </div>
  )
}
export default NewbiePopup
